// Angular
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { NgbDate, NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { DatePipe } from '@angular/common'
import { PartialsModule } from '../../partials/partials.module';
import { MaterialPreviewModule } from '../../partials/content/general/material-preview/material-preview.module';
  // tslint:disable-next-line:max-line-length
import { SettingsService, PatientsService, DoctorsService, DataSharingService, AssociationsService, PaymentsService } from '../../../core/application';


// Import AG Grid
import { AgGridModule } from 'ag-grid-angular';

// addons
import { MatInputModule } from '@angular/material';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap'

import { AngularSlickgridModule } from 'angular-slickgrid'
import { NgxPrintModule } from 'ngx-print';
import { PaymentComponent } from './payment.component';
import { AddPaymentComponent } from './add-payment/add-payment.component';
import { PatientsCreditComponent } from './patients-credit/patients-credit.component';
import { PaymentsHistoryComponent } from './payments-history/payments-history.component';

const routes: Routes = [
  {
    path: '',
    component: PaymentComponent,
    children: [
      {
        path: '',
        redirectTo: 'add-payment',
        pathMatch: 'full'
      },
      {
        path: 'add-payment',
        component: AddPaymentComponent
      },
      {
        path: 'patients-credit',
        component: PatientsCreditComponent
      },
      {
        path: 'payments-history',
        component: PaymentsHistoryComponent
      }
    ]
  },
];

@NgModule({
  imports: [
    CommonModule,
    HttpClientModule,
    RouterModule.forChild(routes),
    FormsModule,
    ReactiveFormsModule,
    PartialsModule,
    MaterialPreviewModule,
    NgbModule,
    MatInputModule,
    AgGridModule.withComponents([]),
    AngularSlickgridModule.forRoot(),
    NgxPrintModule
  ],
  // tslint:disable-next-line:max-line-length
  providers: [SettingsService, PatientsService, DoctorsService, AssociationsService, NgbActiveModal, DataSharingService, DatePipe, PaymentsService],
  declarations: [
    PaymentComponent,
    AddPaymentComponent,
    PatientsCreditComponent,
    PaymentsHistoryComponent
  ]
})
export class PaymentModule { }
