import { Component, OnInit } from '@angular/core';
import { SettingsService } from '../../../core/application';

@Component({
  selector: 'kt-payment',
  templateUrl: './payment.component.html',
  styleUrls: ['./payment.component.scss']
})
export class PaymentComponent implements OnInit {

  constructor(private settingsService: SettingsService) { }

  ngOnInit() {
    this.settingsService.getAll('PaymentType').subscribe(
      res => {
        localStorage.setItem('PaymentType', JSON.stringify(res))
      },
      err => console.log(err)
    );
    this.settingsService.getAll('PaymentStatus').subscribe(
      res => {
        localStorage.setItem('PaymentStatus', JSON.stringify(res))
      },
      err => console.log(err)
    );
  }
}
