import { Component, OnInit } from '@angular/core';
import { AngularGridInstance, GridService, GridOption, Column, OnEventArgs, Formatter } from 'angular-slickgrid';
import { PaymentsService } from '../../../../core/application';
import { DatePipe } from '@angular/common';
import { Router } from '@angular/router';


const currencyFormatter: Formatter = (row: number, cell: number, value: any, columnDef: Column, dataContext: any, grid: any) => {
  return `${value.toFixed(2)}`;
};

@Component({
  selector: 'kt-payments-history',
  templateUrl: './payments-history.component.html',
  styleUrls: ['./payments-history.component.scss']
})
export class PaymentsHistoryComponent implements OnInit {
  invoicesGrid: AngularGridInstance;
  grid: any;
  gridService: GridService;
  dataView: any;

  columnDefinitions: Column[] = [];
  gridOptions: GridOption = {};
  dataset: any = [];

  date: any;
  startDate: any;
  endDate: any;

  constructor(
    private mSerivce: PaymentsService,
    private router: Router,
    private datePipe: DatePipe) { }

  ngOnInit() {
    this.date = this.datePipe.transform(new Date(), 'dd-MM-yyyy');
    this.columnDefinitions = [
      {
        id: 'id', name: 'Id', field: 'id', sortable: true, width: 30,
        onCellClick: (e: Event, args: OnEventArgs) => {
          const id = args.dataContext['id']
          var data;

        }
      },
      { id: 'payment_code', name: 'Code', field: 'payment_code', sortable: true, width: 70, filterable: true },
      { id: 'name', name: 'Nom', field: 'patient.full_name', sortable: true, width: 170, filterable: true },
      { id: 'phone_number', name: 'Num Tel', field: 'patient.mobile_phone_number_1', width: 70, filterable: true },
      { id: 'payment_date', name: 'Date', field: 'payment_date' },
      { id: 'payment_amount', name: 'Montant de paiement', field: 'payment_amount', formatter: currencyFormatter },

    ];
    this.gridOptions = {
      enableAutoResize: true,       // true by default
      enableCellNavigation: true,
      enableFiltering: true,
      enablePagination: true,
      pagination: {
        pageSizes: [10, 15, 20, 25, 30, 40, 50, 75, 100],
        pageSize: 10,
        totalItems: 0
      },
      dataItemColumnValueExtractor: function getItemColumnValue(item, column) {
        var val = undefined;
        try {
          val = eval("item." + column.field);
        } catch (e) {
          // ignore
        }
        return val;
      }
    };

    // fill the dataset with your data
    let date = new Date();
    let firstDay = this.datePipe.transform(new Date(date.getFullYear(), date.getMonth(), 1), 'yyyy-MM-dd');
    let lastDay = this.datePipe.transform(date, 'yyyy-MM-dd');

    this.mSerivce.getPaymentsHistory({ start_date: firstDay, end_date: lastDay }).subscribe(
      res => this.dataset = res,
      err => console.log('errro'),
      () => console.log('finish')
    )
  }


  GridReady(angularGrid: AngularGridInstance) {
    this.invoicesGrid = angularGrid;
    this.dataView = angularGrid.dataView;
    this.grid = angularGrid.slickGrid;
    this.gridService = angularGrid.gridService;
  }


  getData() {
    this.mSerivce.getPaymentsHistory({
      start_date: this.startDate.year + '/' + this.startDate.month + '/' + this.startDate.day,
      end_date: this.endDate.year + '/' + this.endDate.month + '/' + this.endDate.day
    }).subscribe(
      res => this.dataset = res,
      err => console.log(err),
      () => console.log('finish')
    );
  }



}